import { Component, ViewChild } from '@angular/core';
import { SplashScreen } from '@ionic-native/splash-screen';
import { StatusBar } from '@ionic-native/status-bar';
import { TranslateService } from '@ngx-translate/core';
import { Config, Nav, Platform } from 'ionic-angular';
import { App } from 'ionic-angular';
 import { AlertController } from 'ionic-angular';
import { FirstRunPage } from '../pages';
import { Settings } from '../providers';
import { HardwareButtons } from '@scaffold-digital/ionic-hardware-buttons';





 
@Component({



  template: `<ion-menu [content]="content" type="overlay">
  <style>
.masters {
 
   background: url('assets/img/steel.jpg') repeat ;
}
.toolbar{
    background-image: url('assets/img/steel.jpg');
    background-size: cover;
    background-position: center center;
  
}
</style>


    <ion-header>
    
      
      <table>
      
      <tr><td > <img height="50" width="50" src="assets/img/icon/menu.png"/></td>
      <td>
    
     
    
      <table>
      
      <tr><td >
      
      <font   face="Cambria" color="#6d071a" size="2"> <b> </b></font> </td></tr> 
      <tr><td > <font   face="Times New Roman" color="0f056b" size="6"> <b>Menu</b></font>  
        </td></tr>
       
        </table> 
      </td></tr>
      
      
      
      </table> 
       
       
       
      
     
    </ion-header>





    <ion-content   class="masters"    >
     
      <ion-list   >
      
      
      
      
        <button  class="masters"    menuClose ion-item *ngFor="let p of pages" (click)="openPage(p)">
       <ion-avatar item-start>
<img width="70" height="70" src="{{p.image}}" alt=""/>
        </ion-avatar>
         <h2><b><font   face="Cambria" color="0000" size="4"> {{p.title}} </font></b></h2>
        </button>
         
         
          <button  class="masters"    menuClose ion-item *ngFor="let p of pages1" (click)="presentConfirm()">
       <ion-avatar item-start>
<img width="70" height="70" src="{{p.image}}" alt=""/>
        </ion-avatar>
         <h2><b><font   face="Cambria" color="0000" size="4"> {{p.title}} </font></b></h2>
        </button>
         
         
         
          
         
         
      </ion-list>
      
      
  
      
      
      
     
      
    </ion-content>

  </ion-menu>
  <ion-nav #content [root]="rootPage"></ion-nav>`
})
export class MyApp {
  rootPage = FirstRunPage;
public alertShown:boolean = false;
  @ViewChild(Nav) nav: Nav;

  pages: any[] = [
    { title: 'Tutorial', component: 'TutorialPage' , image: 'assets/img/icon/tutorial.png'},
    { title: 'Home', component: 'WelcomePage' , image: 'assets/img/icon/home.png'},
    { title: 'Products', component: 'TabsPage' , image: 'assets/img/icon/products.png' },
    
    { title: 'About', component: 'AboutPage', image: 'assets/img/icon/about.png' },
    { title: 'Quality', component: 'QualityPage', image: 'assets/img/icon/quality.png' },
    
    { title: 'Contact', component: 'ContactPage', image: 'assets/img/qaprofil/phone.png' }
       
  ]
  
  
    pages1: any[] = [
    { title: 'Exit', component: 'TutorialPage' , image: 'assets/img/exit.png' }
       
  ]
  
  
  
  

  constructor(private translate: TranslateService, public platform: Platform, settings: Settings, private config: Config, private statusBar: StatusBar, private splashScreen: SplashScreen, public alertCtrl: AlertController, public app: App, hardwareButtons: HardwareButtons) {
    platform.ready().then(() => {
    hardwareButtons.init();
      // Okay, so the platform is ready and our plugins are available.
      // Here you can do any higher level native things you might need.
      this.statusBar.styleDefault();
      this.splashScreen.hide();
    });
    this.initTranslate();
    
   
   
 platform.ready().then(() => {
      statusBar.styleDefault();
      splashScreen.hide();
      platform.registerBackButtonAction(() => {
        if (this.alertShown==false) {
          this.presentConfirm();  
        }
      }, 0)
    });
    
    
    
    
    
    
    
    
    
    
    
    
    
  }
  

      
  presentConfirm() {
    let alert = this.alertCtrl.create({
      title: 'Confirm Exit',
      message: 'Do you want Exit?',
      buttons: [
        {
          text: 'Cancel',
          role: 'cancel',
          handler: () => {
            console.log('Cancel clicked');
            this.alertShown=false;
          }
        },
        {
          text: 'Yes',
          handler: () => {
            console.log('Yes clicked');
            this.platform.exitApp();
          }
        }
      ]
    });
     alert.present().then(()=>{
      this.alertShown=true;
    });
 }
 
 
 

  initTranslate() {
    // Set the default language for translation strings, and the current language.
    this.translate.setDefaultLang('en');
    const browserLang = this.translate.getBrowserLang();

    if (browserLang) {
      if (browserLang === 'zh') {
        const browserCultureLang = this.translate.getBrowserCultureLang();

        if (browserCultureLang.match(/-CN|CHS|Hans/i)) {
          this.translate.use('zh-cmn-Hans');
        } else if (browserCultureLang.match(/-TW|CHT|Hant/i)) {
          this.translate.use('zh-cmn-Hant');
        }
      } else {
        this.translate.use(this.translate.getBrowserLang());
      }
    } else {
      this.translate.use('en'); // Set your language here
    }

    this.translate.get(['BACK_BUTTON_TEXT']).subscribe(values => {
      this.config.set('ios', 'backButtonText', values.BACK_BUTTON_TEXT);
    });
  }

  openPage(page) {
    // Reset the content nav to have just this page
    // we wouldn't want the back button to show in this scenario
    this.nav.setRoot(page.component);
  }
}



